export default {
    onLoad: function(option) { //option为object类型，会序列化上个页面传递的参数
        this.getData(option.dy_task_id);
        this.dy_task_id = option.dy_task_id;
        this.formData.dy_task_id = option.dy_task_id;
    },
    created(){
      // 为图片上传添加请求头
        uni.getStorage({
            key:'token',
            success:(res)=>{
                this.headerData.token=res.data
            }
        })
    },
    data() {
        return {
            URL:getApp().globalData.URL,
            //抖音数据
            dy_task_id: null,
            dyTaskDetail: {},
            //是否选择了图片
            isImg: false,
            // 上传路径
            action: 'http://121.42.13.36:9000/api/v1/create_dy_task_order',
            url: null,
            formData: {
                dy_task_id: this.dy_task_id
            },
            headerData:{
                token:null
            }

        };
    },
    methods: {
        // 请求详细数据
        async getData(dy_task_id) {
            await this.$u.get('get_dy_task_detail', {
                dy_task_id
            }).then(res => {
                this.dyTaskDetail = res.data;
                this.url = res.data.dy_url
            })
        },
        uploaded(lists) {
           
        },
        oversize() {
            uni.showToast({
                title: "图片过大，请重新上传",
                icon: 'none'
            })
        },
        copy() {
            uni.setClipboardData({
                data: this.url,
                success: ()=>{
                    uni.showToast({
                        title: "复制成功:"+this.url,
                        icon: 'none'
                    })
                }
            });
        },
        open() {
            plus.runtime.openURL(this.url, (res) =>{
                console.log(res)
                uni.showToast({
                    title: "跳转被阻止！请放行",
                    icon: 'none'
                })
            });
        },


        submitDyTask() {
            // 验证是否有图片
            if (this.isImg === false) {
                uni.showToast({
                    title: "请提交图证",
                    icon: "none"
                })
                return;
            }
            // 验证id
            if (this.dy_task_id === null) {
                uni.showToast({
                    title: "ID为NULL",
                    icon: "none"
                })
                return;
            }
            // 上传
            this.$refs.uUpload.upload();
        },
        //图片上传成功
        success(data) {
            let data2=JSON.parse(data)
             if(data2.errorCode===0){
                 uni.showToast({
                     title:data2.msg
                 })
                 setTimeout(()=>{
                     uni.switchTab({
                         url:'../home/home'
                     })
                 },1500)
                 return;
             }
             if(data2.errorCode===20010){
                 uni.showToast({
                     title:data2.msg,
                     icon:'none'
                 })
                setTimeout(()=>{
                    uni.switchTab({
                        url:'../home/home'
                    })
                },1500)
                 return;
             }
        },
        //图片上传失败
        error(res) {
       
                uni.showToast({
                    title:"遇到未知错误",
                     icon:'none'
                })
            
        },
        //
        complete(list) {
            this.isImg = true
        },
        remove(index) {
            this.isImg = false
        }
    }
}
